    text-align: left;
  }
}

@media only screen and (min-width: 1200px) {
  .hedaing-wrap {
    margin-top: 15px;
  }
}
.hedaing-wrap .date-wrapper .active {
  border: 1px solid #0f121b;
  background-color: #f0862e;
  color: #ffffff;
}
.hedaing-wrap .date-wrapper button {
  background-color: #ffffff;
  color: #f0862e;
  border: 1px solid #D9D9D9;
  width: 120px;
  height: 42px;
  border-radius: 4px;
}
.hedaing-wrap .active {
  margin: 15px 15px;
}
@media only screen and (max-width: 575px) {
  .hedaing-wrap .active {
    margin: 5px 15px;
  }
}
.hedaing-wrap .active h1 {
  color: #0f121b;
  font-weight: 600;
  font-size: 1.5rem;
}
.hedaing-wrap #deliveryHeading, .hedaing-wrap #pickupHeading {
  margin: 15px;
}
.hedaing-wrap #deliveryHeading h1, .hedaing-wrap #pickupHeading h1 {
  font-size: 1.5rem;
  color: #0f121b;
}
@media only screen and (max-width: 575px) {
  .hedaing-wrap #deliveryHeading h1, .hedaing-wrap #pickupHeading h1 {
    font-size: 1.25rem;
  }
}

.chooseDateTable .head-part .slot-grid--date {
  padding: 6px 30px 2px 30px;
}

@media only screen and (max-width: 575px) {
  .head-part {
    /* overflow-x: scroll;*/
  }
}
@media only screen and (max-width: 575px) {
  .html-product-details-page .product-review {
    padding: 10px;
  }
}
@media only screen and (max-width: 575px) {
  .share-btn {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
@media only screen and (max-width: 575px) {
  .share-btn .product-share-button {
    height: 21px;
    margin-left: 2px;
  }
}
.share-btn .product-share-button .addthis_toolbox addthis_default_style {
  margin-top: 4px;
}

@media only screen and (max-width: 575px) {
  .subscribe-box-wrapper .news-wrapper .info,
.subscribe-box-wrapper .newsletter-subscribe .newsletter-email {
    text-align: center;
  }
}
.slot_header_section .hedaing-wrap {
  padding-top: 10px;
}

.delivery-section .modal-content {
  top: 300px;
}

.review-info .product-review-box .rating .rating-value {
  display: none;
}

.no-data {
  text-align: center;
}

.likewrapper {
  text-align: center;
}
.likewrapper .like .like-button:before {
  content: "";
  background: url(../../Content/images/new-icon/like.png) no-repeat;
  top: -14px;
  left: -28px;
}
@media only screen and (max-width: 575px) {
  .likewrapper .like .like-button:before {
    background-size: 15px;
  }
}
.likewrapper .like .like-button:hover:before {
  content: "";
  background: url(../../Content/images/latest-img/like-svgrepo-com.svg) no-repeat;
}
.likewrapper .question {
  font-style: normal;
  font-size: 0.937rem;
  color: #8c8c8c;
}
@media only screen and (max-width: 575px) {
  .likewrapper .question {
    font-size: 0.812rem;
  }
}
@media only screen and (max-width: 575px) {
  .likewrapper .product-review-helpfulnesss {
    margin: 7px;
    margin-top: 0px;
  }
}
.likewrapper .dislike button, .likewrapper .like button {
  border: none;
  font-size: 0;
  background-color: #ffffff;
  position: relative;
}
.likewrapper .dislike button:before, .likewrapper .like button:before {
  content: "";
  width: 30px;
  height: 30px;
  position: absolute;
  background: url(../../Content/images/new-icon/dislike.png) no-repeat;
  top: -10px;
  right: -40px;
}
@media only screen and (max-width: 575px) {
  .likewrapper .dislike button:before, .likewrapper .like button:before {
    background-size: 15px;
  }
}
.likewrapper .dislike button:hover:before, .likewrapper .like button:hover:before {
  content: "";
  background: url(../../Content/images/latest-img/dislike-svgrepo-com.svg) no-repeat;
}

.product-review-item .review-info .toggle-text {
  max-width: 100%;
  margin: 50px auto;
  text-align: center;
}
.product-review-item .review-info .toggle-text .toggle-text-content a {
  display: inline-block;
  margin: 0px;
  padding: 0px;
  color: #f0862e;
}
.product-review-item .review-info .toggle-text .toggle-text-content span {
  display: none;
}

.share-btn .product-share-button {
  width: 100%;
}
@media only screen and (max-width: 575px) {
  .share-btn .product-share-button .addthis_toolbox {
    display: flex;
    width: 100%;
    justify-content: center;
  }
}
.share-btn .product-share-button .addthis_toolbox {
  display: flex;
  align-items: self-start;
  justify-content: center;
}
@media only screen and (min-width: 1200px) {
  .share-btn .product-share-button .addthis_toolbox {
    justify-content: left;
  }
}
.share-btn .product-share-button .addthis_toolbox .title {
  line-height: 18px;
}

.toggle-text {
  max-width: 100%;
  margin: 50px auto;
  text-align: center;
}
.toggle-text .toggle-text-content .toggle-text-link {
  display: inline-block;
  margin: 0px;
  padding: 0px;
  color: #f0862e;
}
.toggle-text .toggle-text-content span {
  display: none;
}